.container {
  max-width: 700px;
  margin: 20px auto;
  font-family: Arial, sans-serif;
}

.startButton {
  padding: 8px 16px;
  font-size: 16px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.startButton.stop {
  background-color: red;
}

.transcript {
  margin-top: 10px;
  font-size: 14px;
  color: #333;
  white-space: pre-wrap;
  word-break: break-word;
  max-height: 200px;
  overflow-y: auto;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 4px;
}
